@import "../scss-rtl/variables";

/*-------- Header Styles ----------*/

/*Dark-header*/

.dark-hor-header {
	.header {
		background: #141026;
		box-shadow: 16px 0px 16px rgba(26, 21, 52, 0.3);
	}

	.nav-link.icon .header-icon,
	.app-sidebar__toggle .header-icon {
		background: #141026;
		box-shadow: 0px 2px 3px #040407;
		border: 1px solid rgba(255, 255, 255, 0.1);
	}

	.header-icon {
		color: #ffffff;
		fill: #ffffff;
	}

	.profile-dropdown img {
		border: 2px solid rgba(255, 255, 255, 0.2);
		background: transparent;
	}

	.hor-header .header-brand-img {
		&.desktop-lgo {
			display: none;
		}

		&.dark-logo {
			display: block;
			margin-top: 5px;
		}
	}
}

@media (min-width: 992px) {
	.dark-hor-header .header .form-inline .form-control {
		background: rgba(255, 255, 255, 0.1);
		border: 1px solid rgba(255, 255, 255, 0.05);

		&::-webkit-input-placeholder {
			color: #ffffff;
			opacity: 1;
		}
		&::-moz-placeholder {
			color: #ffffff;
			opacity: 1;
		}
		&:-ms-input-placeholder {
			color: #ffffff;
			opacity: 1;
		}
		&::-ms-input-placeholder {
			color: #ffffff;
			opacity: 1;
		}
		&:focus {
			color: $white;
		}
	}
}

@media (max-width: 575.98px) {
	.dark-hor-header {
		.header .header-brand-img.mobile-logo,
		.hor-header .header-brand-img.dark-logo {
			display: none;
		}

		.header .header-brand-img.darkmobile-logo {
			display: block;
			margin-top: 5px;
			margin-left: 0;
		}
	}
}

@media (max-width: 992px) {
	.dark-hor-header {
		.navsearch svg {
			background: rgba(255, 255, 255, 0.1);
			box-shadow: 0px 2px 3px rgba(26, 21, 52, 0.3);
			border: 1px solid rgba(255, 255, 255, 0.05);
			color: #ffffff;
			fill: #ffffff;
		}

		.animated-arrow {
			&.hor-toggle {
				background: rgba(255, 255, 255, 0.1);
				box-shadow: 0px 2px 3px rgba(26, 21, 52, 0.3);
				border: 1px solid rgba(255, 255, 255, 0.05);
				color: #ffffff;
				fill: #ffffff;
			}

			span {
				&:before,
				&:after {
					background: #ffffff;
				}

				background: #ffffff;
			}
		}

		.app-header .header-brand-img.mobile-logo {
			display: none;
		}
	}
}

